<script>
  /* eslint eqeqeq: "off", camelcase: "off" */

  import { timeout } from 'utils'
  import {
    popup_show_lucky_draw_prize
  } from './popups.js'

  export default {
    data () {
      return {
        isSwitch: false,
        timer: null,
        animating: false,
        end: false,
        gotPrize: false,
        prize: 0,
        otherPrizesList: [],
        allPrizes: [],
        rollIndex: 0,
        testDom: true,
        sliderPostionsList: [],
        sliderRowHeight: 10,
        sliderRowMargin: 18,
        items: [1, 2, 3, 4, 5, 6, 7, 8, 9],
        nextNum: 10,
        selectedPrizes: [

          {amount: 10, selected: false},
          {amount: 0.1, selected: false},
          {amount: 8, selected: false},
          {amount: 0.2, selected: false},
          {amount: 5, selected: false},
          {amount: 0.3, selected: false},
          {amount: 3, selected: false},
          {amount: 0, selected: false}

        ],
        currentSelection: null
      }
    },

    mounted () {
      $api.luckyDrawAwardees()
        .then(res => res.data && res.data.payload)
        .then(payload => {
          this.allPrizes = payload
          this.otherPrizesList = this.allPrizes.slice(0, 10)
          this.run()
        })
    },

    methods: {
      // 滚动效果
      run () {
        this.sliderPostionsList = [{top: 1}, {top: this.otherPrizesList.length * (this.sliderRowHeight + this.sliderRowMargin)}, {top: 2 * this.otherPrizesList.length * (this.sliderRowHeight + this.sliderRowMargin)}]
        this.rollInterval = setInterval(this.roll, 1000)
        this.rollInterval2 = setInterval(this.updateList, this.otherPrizesList.length * 1000)
      },

      roll () {
        for (var i = 0; i < this.sliderPostionsList.length; i++) {
          if (this.sliderPostionsList[i]) {
            this.sliderPostionsList[i].top -= this.sliderRowHeight + this.sliderRowMargin
          }
        }
      },

      updateList () {
        this.sliderPostionsList.push({top: 2 * this.otherPrizesList.length * (this.sliderRowHeight + this.sliderRowMargin)})
        setTimeout(() => {
          this.sliderPostionsList[this.rollIndex] = null
          this.rollIndex ++
        }, 100)
      },

      setPosition (pos) {
        return pos == 0 ? '1px' : pos + 'px'
      },

      setSelected (index) {
        index = (index + 1) % 8
        for (let i = 0; i < this.selectedPrizes.length; i++) {
          this.selectedPrizes[i].selected = false
        }
        this.selectedPrizes[index].selected = true
        this.currentSelection = this.selectedPrizes[index]
      },

      animate () {
        let step = 0
        let duration = 3000
        let delay = 90
        if (this.end || this.animating) return
        this.animating = true
        this.getReward()
        this.timer = setInterval(() => {
          this.isSwitch = !this.isSwitch
          this.setSelected(step)
          step++
          if ((delay * step >= duration) && (this.gotPrize)) {
            if (this.prize == this.currentSelection.amount) this.stopOnAmount()
          }
        }, delay)
      },

      getReward () {
        $api.luckyDrawReward()
          .then(
            (res) => {
              let payload = (res.data && res.data.payload) ? res.data.payload : {}
              this.gotPrize = true
              this.prize = payload.reward
              this.taskName = payload.title
            }
            , (error) => {
              console.log('has error---', error)
              this.stopOnError()
            }
          )
      },

      stopOnError () {
        this.animating = false
        clearInterval(this.timer)
      },

      stopOnAmount () {
        this.animating = false
        this.end = true
        clearInterval(this.timer)
        timeout(500).then(() => {
          let popup = $popup.fromTemplate(
            popup_show_lucky_draw_prize.template({amount: this.prize, taskName: this.taskName}),
            popup_show_lucky_draw_prize.options
          )
          popup.show().then((btn) => {
            if (btn === 0) {
              this.back()
            }
          })
        })
      },

      beforeDestroy () {
        clearInterval(this.rollInterval)
        clearInterval(this.rollInterval2)
        clearInterval(this.timer)
      },

      back () {
        setTimeout(() => {
          $router.back({path: '/tasks'})
        }, 200)
      }
    }
  }
</script>



