/* eslint eqeqeq: "off", camelcase: "off" */

// 安装证书弹层
const popup_show_lucky_draw_prize = {
  options: {
    effect: 'scale',
    cssClass: 'popup-task-reward',
    buttons: [
      {text: '很好，继续赚钱', theme: 'assertive'}
    ]
  },
  template: function (options) {
    let amount = options.amount || '0.0'
    let taskName = options.taskName || ''

    return `
      <img class="img-lucky-draw-finished" src="//qianka.b0.upaiyun.com/images/782d55e2f4df935349e07273c06a11f1.png" alt="">
      <scalable class="reward" height="62">
        <span class="rmb">￥</span>
        <span class="amount">${parseFloat(amount)}</span>
        <span class="yuan">元</span>
      </scalable>
      <div class="task-info">
        <div class="line"></div>
        <div class="task-type">彩蛋任务</div>
        <div class="task-name">${taskName}</div>
      </div>
    `
  }
}

// 放弃彩蛋任务弹层
const popupGiveUpTask = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '放弃' },
      { text: '不放弃' }
    ]
  },
  template: '<div class="desc no-title">确定要放弃当前任务？</div>'
}

module.exports = {
  popup_show_lucky_draw_prize,
  popupGiveUpTask
}



